.hamburgerBtn,
.hamburgerBtn::before,
.hamburgerBtn::after {
  position: absolute;
  top: 1.5rem;
  width: 25px;
  height: 3px;
  background-color: #c4c4c4;
  cursor: pointer;
  transition: all 400ms ease-in-out;
  z-index: -1;
}

.wrapHamburger {
  width: 25px;
  height: 25px;
  cursor: pointer;
  margin: 0 2rem;
}

.hamburgerBtn::before {
  display: block;
  content: "";
  top: -5px;
  cursor: pointer;
  z-index: -1;
}

.hamburgerBtn::after {
  display: block;
  content: "";
  top: 5px;
  cursor: pointer;
  z-index: -1;
}

.hamburgerBtnIsActive {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
  transition: all 400ms ease-in-out;
}
