.wrapDesktopNavigation {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin: 0 3rem;
}

.wrapMobileNavigation {
  display: flex;
  justify-content: center;
  position: absolute;
  right: 0;
  top: -100vh;
  transition: all 400ms ease-in-out;
  width: 100%;
  height: 50vh;
  background-color: rgba(128, 128, 128, 0.25);
}

.activeMobileNav {
  top: 3rem;
  transition: all 400ms ease-in-out;
}

.desktopNavBar {
  display: flex;
  list-style-type: none;
  padding: 0;
}

.mobileNavBar {
  list-style-type: none;
  padding: 0;
  list-style-type: none;
  padding: 0;
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.desktopNavBarItems {
  margin: auto 1rem;
  color: #333539;
}

.mobileNavBarItems {
  color: #333539;
  font-size: large;
}

.desktopNavBarItems:hover,
.mobileNavBarItems:hover {
  color: #e4282f;
  cursor: pointer;
}
