.wrapHeader {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 3rem;
  background: rgba(255, 255, 255, 0.7);
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.05);
  position: fixed;
}

@media (max-width: 767px) {
  .wrapHeader {
    justify-content: space-between;
  }
}
